import React from 'react';
import { Formik, Field, Form, ErrorMessage } from "formik";
import * as Yup from "yup";


const Loginform = (props) => {

    const {onSubmitProp}=props;

    return (
        <div >
          <Formik
          initialValues={{
          email:'',
          password:'',
        }}
        validationSchema={Yup.object().shape({
            email: Yup.string()
            .email("Correo no valido")
            .min(3, "Este correo electrnico es incorrecto")
            .required("Por favor, ingresa un correo electrnico vlido"),
            
            password: Yup.string()
            .min(8, "La clave debe contener ms de 8 caractes")
            .required("Por favor ingrese una contrasea")
        })}

        onSubmit={(values, {setSubmitting}) =>{
            const timeOut = setTimeout(( )=>{
                console.log(values);
                onSubmitProp(values);
                setSubmitting(false);
                clearTimeout(timeOut);
            }, 1000);
        }}
        >
            {({
                values,
                errors,
                touched,
                handleSubmit,
                //isSubmitting,
                //validating,
                valid,
            }) =>{
        return (
            <div>
                <Form className= "contact" method= "post" onSubmit={handleSubmit}>
                    <label htmlFor="email" className="col-form-label">Correo Electrnico</label>
                    <Field id='loginemail' type="text" placeholder="Email" className={`form-control`} name='email'/>
                    <ErrorMessage name="email">{(msg) => <p>{msg}</p>}</ErrorMessage>

                    <label htmlFor="password" className="col-sm-2 col-form-label">Contrasea</label>
                    <Field  id='loginpassword' type="password" placeholder="Contrasea" className={`form-control`} name='password'/>
                    {errors.password && touched.password && <p>{errors.password}</p>}
                    <br></br>
                    <button type="submit" disabled={Object.values(errors).length > 0} className="btn btn-primary rounded-pill border border-dark">Login</button>
                </Form>
                </div>
        );
        }}
        </Formik>
        </div>
    );
}

export default Loginform;
